import {mapState,mapMutations} from 'vuex'
export default{
	data(){
		return{
			sessionId:'',
			myData:[]
		}
	},
	computed: mapState(['hasLogin','uerInfo']), 
	onLoad() {
		this.init()
	},
	methods:{
		...mapMutations(['login']),
		
		// 页面渲染
		init(){
			uni.getStorage({
				key:'userInfo',
				success:(res)=>{
					this.login(res.data);
					this.sessionId = res.data.sessionId
					uni.request({
						url:'https://www.nssnail.com/wx/getAuction',
						data:{
							sessionId:this.sessionId
						},
						success: (res) => {
							if(res.data.stat == 1){
								this.myData = res.data.data
								this.myData.forEach((item)=>{
									if(item.stat == 1){
										item.stat = '未拍卖'
									}else if(item.stat == 2){
										item.stat = '拍卖中'
									}else if(item.stat == 3){
										item.stat = '已拍卖'
									}else{
										item.stat = '失效'
									}
								})
							}else if(res.data.stat == 3 || res.data.stat == 4){
								uni.showModal({
								    title: '提示',
								    content: '登录过期，请重新登录',
								    success: function (res) {
								        if (res.confirm) {
								            uni.navigateTo({
								            	url:'../myInit/myInit'
								            })
								        }
								    }
								});
							}
							
						}
					})
				}
			})
		},
		
		// 跳转至详情页
		gotoBuyDetail(item){
			console.log(item)
			var id = item.id
			uni.navigateTo({
				url:'../buyDetail/buyDetail?id='+id+'&sessionId='+this.sessionId
			})
		}
	}
}
